//AQUI VAMOS A TRAER EL MODULO QUE MUESTRA LA INFO INICIAL DEL PRODUCTO
//Y LE DAMOS LA FUNCION EL CLICK ROUTER

import { Component, OnInit } from '@angular/core';
import { DesignOthersPhotoServiceComponent } from './shared/design-others-photo-service.component';
import { ToastrService } from './common/services/toastr.service';

declare let toastr
@Component({
  selector: 'photo-app-design-others-list',
  template: `
      <div *ngFor="let designothersphoto of designothersphotos">
        <design-others-photo-list (click)="handeDesignOthersClick(designothersphoto.name)" [designothersphoto]="designothersphoto"></design-others-photo-list>
      </div>
  `,
})
export class PhotoDesignOthersListComponent implements OnInit {
  designothersphotos: any[]
  constructor(private DesignOthersPhotoServiceComponent: DesignOthersPhotoServiceComponent, private toastr:ToastrService) {
   }

  ngOnInit() {
    this.designothersphotos =  this.DesignOthersPhotoServiceComponent.getDesignOthersPhotos()
  }

  handeThumbnailClick(designothersphotoName)  {
    this.toastr.success(designothersphotoName)
  }
}
