//ROUTER MODULE - COMPONENTS USER ROUTER
import { Routes } from '@angular/router';//importando el Route
import { NoFoundComponent } from './modules/common/errors/no-found.component';

//Default components router
import { InicioComponent } from './modules/inicio/inicio.component';
import { VideosDetailComponent } from './modules/inicio/videos-detail/videos-detail.component';
import { VideoRouteActivator } from './modules/inicio/videos-detail/videos-router-activate.service';//activando el error 404 en los items* (este es el 404 especial para los items)

//Trends components router
import { TrendsComponent } from './modules/trends/trends.component';
import { WebDesignComponent } from './modules/web-design/web-design.component';
import { ColorPaletteComponent } from './modules/color-palette/color-palette.component';
import { DirectosComponent } from './modules/directos/directos.component';
import { UxDesignerComponent } from './modules/ux-designer/ux-designer.component';

//Photos components router
import { ViewPhotoComponent } from './modules/photos/view-photo.component';
import { DesignUxPhotosDetailComponent } from './modules/photos/categories/ux-design-items/design-ux-photos-detail/design-ux-photos-detail.component';
import { DesignUxPhotoRouteActivator } from './modules/photos/categories/ux-design-items/design-ux-photos-detail/design-ux-photos-router-activate.service';

import { PhotoDesignWebListComponent } from './modules/photos/categories/design-web-items/photo-design-web-list.component';
import { DesignWebPhotosDetailComponent } from './modules/photos/categories/design-web-items/design-web-photos-detail/design-web-photos-detail.component';
import { DesignWebPhotoRouteActivator } from './modules/photos/categories/design-web-items/design-web-photos-detail/design-web-photos-router-activate.service';

import { PhotoDesignGeneralListComponent } from './modules/photos/categories/design-general-items/photo-design-general-list.component';
import { DesignGeneralPhotosDetailComponent } from './modules/photos/categories/design-general-items/design-general-photos-detail/design-general-photos-detail.component';
import { DesignGeneralPhotoRouteActivator } from './modules/photos/categories/design-general-items/design-general-photos-detail/design-general-photos-router-activate.service';

import { PhotoDesignOthersListComponent } from './modules/photos/categories/design-others-items/photo-design-others-list.component';
import { DesignOthersPhotosDetailComponent } from './modules/photos/categories/design-others-items/design-others-photos-detail/design-others-photos-detail.component';
import { DesignOthersPhotoRouteActivator } from './modules/photos/categories/design-others-items/design-others-photos-detail/design-others-photos-router-activate.service';

//Posts components router
import { ViewPostComponent } from './modules/view-post/view-post.component';
import { DesignUxPostsDetailComponent } from './modules/view-post/categories/ux-design-items/design-ux-posts-detail/design-ux-posts-detail.component';
import { DesignUxPostRouteActivator } from './modules/view-post/categories/ux-design-items/design-ux-posts-detail/design-ux-posts-router-activate.service';

import { DesignWebListComponent } from './modules/view-post/categories/design-web-items/design-web-list.component';
import { DesignWebPostsDetailComponent } from './modules/view-post/categories/design-web-items/design-web-posts-detail/design-web-posts-detail.component';
import { DesignWebPostRouteActivator } from './modules/view-post/categories/design-web-items/design-web-posts-detail/design-web-posts-router-activate.service';

import { DesignGeneralListComponent } from './modules/view-post/categories/design-general-items/design-general-list.component';
import { DesignGeneralPostsDetailComponent } from './modules/view-post/categories/design-general-items/design-general-posts-detail/design-general-posts-detail.component';
import { DesignGeneralPostRouteActivator } from './modules/view-post/categories/design-general-items/design-general-posts-detail/design-general-posts-router-activate.service';

import { DesignOthersListComponent } from './modules/view-post/categories/design-others-items/design-others-list.component';
import { DesignOthersPostsDetailComponent } from './modules/view-post/categories/design-others-items/design-others-posts-detail/design-others-posts-detail.component';
import { DesignOthersPostRouteActivator } from './modules/view-post/categories/design-others-items/design-others-posts-detail/design-others-posts-router-activate.service';

//News components router
import { NewsListComponent } from './modules/news/news-list.component';
import { NewssDetailComponent } from './modules/news/newss-detail/newss-detail.component';
import { NewsRouteActivator } from './modules/news/newss-detail/newss-router-activate.service';//activando el error 404 en los items* (este es el 404 especial para los items)

//Record components router
import { RecordComponent } from './modules/record/record.component';

export const appRoutes:Routes = [
    //Default components parameters and router
    {path: 'inicio', component: InicioComponent },
    {path: 'videos/:id', component: VideosDetailComponent, canActivate: [VideoRouteActivator]},//Las id son las finales por que lo que esté debajo lo tomará como id
    //others components router
    {path: 'trends', component: TrendsComponent},
    {path: 'web-design', component: WebDesignComponent},
    {path: 'color-palette', component: ColorPaletteComponent},
    {path: 'directos', component: DirectosComponent},
    {path: 'ux-designer', component: UxDesignerComponent},

    //Photos component parameters and router
    {path: 'view-photo', component: ViewPhotoComponent},
    {path: 'designuxphotos/:id', component: DesignUxPhotosDetailComponent, canActivate: [DesignUxPhotoRouteActivator]},//Las id son las finales por que lo que esté debajo lo tomará como id

    {path: 'design-web-list', component: PhotoDesignWebListComponent},
    {path: 'designwebphotos/:id', component: DesignWebPhotosDetailComponent, canActivate: [DesignWebPhotoRouteActivator]},//Las id son las finales por que lo que esté debajo lo tomará como id

    {path: 'design-general-list', component: PhotoDesignGeneralListComponent},
    {path: 'designgeneralphotos/:id', component: DesignGeneralPhotosDetailComponent, canActivate: [DesignGeneralPhotoRouteActivator]},//Las id son las finales por que lo que esté debajo lo tomará como id

    {path: 'design-others-list', component: PhotoDesignOthersListComponent},
    {path: 'designothersphotos/:id', component: DesignOthersPhotosDetailComponent, canActivate: [DesignOthersPhotoRouteActivator]},//Las id son las finales por que lo que esté debajo lo tomará como id
    
    //Posts component parameters and router
    {path: 'view-post', component: ViewPostComponent},
    {path: 'designuxposts/:id', component: DesignUxPostsDetailComponent, canActivate: [DesignUxPostRouteActivator]},//Las id son las finales por que lo que esté debajo lo tomará como id

    {path: 'design-web-list', component: DesignWebListComponent},
    {path: 'designwebposts/:id', component: DesignWebPostsDetailComponent, canActivate: [DesignWebPostRouteActivator]},//Las id son las finales por que lo que esté debajo lo tomará como id

    {path: 'design-general-list', component: DesignGeneralListComponent},
    {path: 'designgeneralposts/:id', component: DesignGeneralPostsDetailComponent, canActivate: [DesignGeneralPostRouteActivator]},//Las id son las finales por que lo que esté debajo lo tomará como id

    {path: 'design-others-list', component: DesignOthersListComponent},
    {path: 'designothersposts/:id', component: DesignOthersPostsDetailComponent, canActivate: [DesignOthersPostRouteActivator]},//Las id son las finales por que lo que esté debajo lo tomará como id

    //Record component
    {path: 'record', component: RecordComponent},
    //News component parameters and router
    {path: 'news', component: NewsListComponent },
    {path: 'newss/:id', component: NewssDetailComponent, canActivate: [NewsRouteActivator]},//Las id son las finales por que lo que esté debajo lo tomará como id
    //Redirection component and 404
    {path: '', redirectTo: 'inicio', pathMatch:'full'},//Default Page
    {path: '**', component: NoFoundComponent },//Ruta de Error 404, (add botón salvavidas, para mejor ux).
]